<template>
  <div class="system-syncset-elk">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_syncset_elk"
    >
      <mu-form-item prop="hosts" label="" :rules="hostsRules">
        <mu-text-field
            v-model="formData.hosts"
            :rows-min="3"
            multiLine
            :rows-max="5"
            :max-length="500"
          />
      </mu-form-item>
      <mu-form-item prop="user" label="" :rules="userRules">
        <mu-text-field v-model="formData.user" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="password" label="" :rules="passwordRules">
        <mu-text-field v-model="formData.password"
        :action-icon="visibility ? 'visibility_off' : 'visibility'"
          :action-click="() => (visibility = !visibility)"
          :type="visibility ? 'text' : 'password'"
         :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="index" label="" :rules="indexRules">
        <mu-text-field v-model="formData.index" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="status" label="">
        <mu-radio
          v-for="item in dropDownData.status"
          :key="item.id"
          v-model="formData.status"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit"></mu-button>
        <mu-button @click="cancel" small></mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { getConfig, setConfig } from "req/api/serverconfig/configset";
export default {
  name: "system_syncset_elk",
  data() {
    return {
      labelPosition: "right",
      visibility:true,
      hostsRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => this.$validates.checkMoreIPandPort(val),
          message: "ip,,:192.0.1.1:80,192.168.0.1:60"
        },
        {
          validate: val => {
            let array = val ? val.toString().split(",") : [];
            return !this.$validates.isSelfRepeat(array)
          },
          message: ""
        }
      ],
      userRules: [{ validate: val => !!val, message: "" }],
      indexRules: [{ validate: val => !!val, message: "" }],
      passwordRules: [{ validate: val => !!val, message: "" }],
      formData: {
        hosts: "",
        user: "",
        password: "",
        index: "",
        status: true,
        type: "elk"
      },
      dropDownData: {
        status: [
          {
            id: true,
            name: ""
          },
          {
            id: false,
            name: ""
          }
        ]
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  created() {
    this.__getConfig({
      type:"elk"
    })
  },
  methods: {
    submitEdit() {
      this.$refs.system_syncset_elk.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_syncset",
            "?",
            () => {
              this.__setConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_syncset",
        "",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfig(param) {
      getConfig(param)
        .then(res => {
          this.formData.hosts = res.data.hosts;
          this.formData.user = res.data.user;
          this.formData.index = res.data.index;
          this.formData.password = res.data.password;
          this.formData.status = res.data.status;
        })
        .catch(err => {});
    },
    __setConfig(param) {
      setConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-syncset-elk {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





